/******************************* Styles **************************************/

@import url("https://fonts.googleapis.com/css2?family=Josefin+Slab:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap");

/********************* Homepage ****************************/

.App {
 background-color: #FBFAFB;
}

#homepage {
 position: fixed;
 top: 65px;
 width: 100%;
 height: 100%;
 background-size: cover;
 background-repeat: no-repeat;
 background-image: url("../images/homepage.png");
}

#homepage_logo {
  height: 100px;
  width: 285px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20%;
}

.home_sub ul {
  font-family: 'Josefin Slab', serif;
  font-size: 30px;
  list-style-type: none;
  padding: 0;
  text-align: center;
}

.home_sub li {
  display: inline;
  margin-left: 25px;
  margin-right: 25px;
  text-align: center;
  color: white;
}

/********************* Standard Page ****************************/
/***** Navigation + Header *****/

.standard {
 background-color: #FBFAFB;
}

.standard_logo {
  height: 50px;
  width: 142px;
  float: left;
  margin: 10px 0 0 10px;
}

.cart {
  height: 30px;
  width: 30px;
  margin: 0;
}

.nav {
  height: 65px;
  width: inherit;
  background-color: black;
}

.nav ul {
  font-family: 'Josefin Slab', serif;
  font-size: 20px;
  list-style-type: none;
  padding: 5px 0 0 0;
  float: right;
}

.nav li {
  display: inline;
  margin-left: 25px;
  margin-right: 35px;
  text-align: center;
}

.nav a {
  text-decoration: none;
  color: white;
}

.nav a:hover {
  color: #17A4E0;
}

#carttotal {
  background-color: yellow;
  width: 18px;
  height: 18px;
  text-align: center;
  border-radius: 50%;
  font-size: 15px;
  padding: 5px 3px 0px 3px;
  position: absolute;
  top: 12px;
  right: 16px;
}

.heading {
  font-family: 'PT Sans', sans-serif;
  color: #828282;
  font-size: 20px;
}

.standard_content {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: 80px;
}

/********************* Products ****************************/

.product_grid {
  font-family: 'PT Sans', sans-serif;
  grid-template-columns: auto auto auto;
  padding: 10px;
  display: grid;
}

.product {
  margin: 30px;
}

.product_grid h6 {
  font-size: 20px;
  margin: 5px 0 0 0;
}

.product_grid p {
  margin: 5px 0 0 0;
}

/********************* Product Details ****************************/

.item_grid {
  font-family: 'PT Sans', sans-serif;
  grid-template-columns: auto auto auto;
  padding: 10px;
  display: grid;
  float: left;
  margin: 30px 0 0 60px;
}

.item_image {
  margin: 50px;
  color: #828282;
}

.roll_image {
  margin-bottom: 20px;
}

.notselectedimage {
  display: none;
}

.ingredients {
  width: 321px;
}

.notselectedingredients {
  display: none;
}

.item_description {
  margin: 20px 0 0 40px;
  color: #828282;
  font-weight: 400;
  width: 500px;
}

.item_description h1, p {
  margin: 0;
}

.item_description h1 {
  margin: 20px 0 20px 0;
}

#price {
  font-size: 25px;
  margin-bottom: 15px;
}

.item_grid h6 {
  font-size: 20px;
  margin: 5px 0 0 0;
}

.item_grid p {
  margin: 5px 0 0 0;
}

.buttons {
  margin-top: 15px;
  margin-bottom: 15px;
}

.buttons .button {
  background-color: gray;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-right: 10px;
  cursor: pointer;
}

.buttons .button:focus {
  background-color: black;
  outline: none;
}

.buttons .button:hover, .selectedbutton {
  background-color: black;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-right: 10px;
  cursor: pointer;
}

#addcart {
  margin: 0 0 0 20px;
}

/********************* Our Story ****************************/

#story {
  position: fixed;
  top: 65px;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../images/baker.png");
}

#story_description {
  color: white;
  width: 400px;
  margin: 80px 0 0 80px;
}

#story_description h1 {
  font-family: 'Josefin Slab', serif;
  font-size: 50px;
  margin: 0 0 20px 0;
}

#story_description p {
  font-family: 'PT Sans', sans-serif;
  font-size: 16px;
}

/********************* Location + Hours ****************************/

.locationhours {
  margin: 20px 0 0 40px;
  color: #828282;
  font-weight: 400;
  width: 500px;
}


/********************* Cart ****************************/

.column {
  float: left;
  width: 25%;
}

.row {
  margin-left: 80px;
  margin-bottom: 50px;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

.cart_image  {
  height: 200px;
  width: 200px;
}

#item_name {
  font-family: 'PT Sans', sans-serif;
  color: #828282;
  font-size: 30px;
  margin-bottom: 5px;
}

#item_glaze {
  font-family: 'PT Sans', sans-serif;
  color: #828282;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 5px;
}

#item_price {
  font-family: 'PT Sans', sans-serif;
  color: #828282;
  font-size: 25px;
}

#quant_title {
  font-family: 'PT Sans', sans-serif;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

.quant_col {
  text-align: center;
}
